  constructor(private router: Router) {
  }

  ngOnInit() {
    if (this.customScaling == null) {
      this.customScaling = true;
    }
  }

  // èåéä¸­
  hoverMenu(id) {
    if (this.hoverId === id) {
      this.hoverId = null;
    } else {
      this.hoverId = id;
    }
  }

  // ç§»ä¸å»ç
  selectMenu(id) {
    this.selectId = id;

  }

  // æ¸é¤éä¸­
  cleanSel() {
    this.selectId = null;
  }

  // å±å¼
  on() {
    const self = this;
    if (self.menuStatus === 'off') {
      self.hoverId = self.hisHover;

      self.menuStatus = 'on';

    }
  }

  // æ¶ç¼©
  off() {
    const self = this;
    self.hisHover = self.hoverId;
    if (self.customScaling == true) {
      this.menuStatus = 'off';
    }
  }

  showChildMenu(i) {
    if (i === this.hoverId && this.menuStatus === 'on') {
      return 'block';
    } else {
      return 'none';
    }
  }


  selectedApp(uri, hoverId) {
    const self = this;
    this.goTo.emit(uri);
    if (hoverId == null || hoverId == '') {
      self.thisMenuId = 0;
    }
    self.thisMenuId = hoverId;
    // console.log('click');
    // self.router.navigate([uri]);
    return false;
  }


